h6,
p {
  margin: 0;
}

/* ÐšÐ»Ð°ÑÑ Ð´Ð»Ñ Ð¾Ð±Ð½ÑƒÐ»ÐµÐ½Ð¸Ñ Ð±Ð°Ð·Ð¾Ð²Ñ‹Ñ… ÑÐ²Ð¾Ð¹ÑÑ‚Ð² Ñƒ ÑÐ¿Ð¸ÑÐºÐ¾Ð² (ul) */
