// ÐšÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€
.container {
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: 320px) {
    width: 100%;
  }

  @media screen and (max-width: 767.9px) {
    max-width: 320px;
    padding: 0 20px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 32px;
  }

  @media screen and (min-width: 1280px) {
    padding: 0 32px;
    width: 1280px;
  }
}

